.nav {
  display: flex;
  padding: 0 104px;
  justify-content: space-between;
  align-items: center;

  @media (max-width: 800px) {
    & {
      padding: 0 10px;
    }
  }

  &--padding-top {
    padding-top: 25.5px;
  }

  &__toggler {
    display: none;
  }

  &__toggler-label {
    display: block;
    position: absolute;
    padding: 10px;
    background: url(../images/Vector.svg) no-repeat center center;
    width: 18px;
    height: 12px;
    cursor: pointer;
    z-index: 2;
    transition: 0.4s;

    &:hover {
      background: url(../images/vector-hover.svg) no-repeat center center;
    }
  }

  &__call-label--hidden {
    visibility: hidden;
  }

  &__logo {
    width: 130.35px;
    height: 75.77px;

    @media (max-width: 600px) {
      & {
        width: 78.6px;
        height: 45.46px;
      }
    }
  }

  &__call-label {
    position: relative;
    padding: 10px;
    background: url(../images/phone-logo.svg) no-repeat center center;
    width: 18px;
    height: 18px;
    cursor: pointer;
    transition: 1s;
  }

  &__call-label:hover > &__hover::before {
    position: absolute;
    top: 0;
    width: 138px;
    right: 20px;
    content: "+1 234 555-55-55";
    color: #fff;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 12px;
    line-height: 20px;

    @media (max-width: 450px) {
      & {
        width: 80px;
        right: 20px;
        font-size: 6px;
      }
    }

    @media (max-width: 320px) {
      & {
        top: -20px;
        right: 0;
      }
    }
  }

  &__toggler--menu {
    position: absolute;
  }

  &__list {
    position: absolute;
    top: 0;
    left: -120%;
    opacity: 0;
    transition: 0.5s;
  }

  &__toggler--menu:checked ~ &__list {
    left: 0;
    opacity: 1;
  }

  &__toggler--menu:checked + &__toggler-label {
    background-image: url(../images/exit.svg);
    width: 16px;
    height: 16px;
  }

}
